.crewCard_container{
    background-color: rgb(255, 235, 235);
    padding:2% 0%;
}
.crewCard{
    width:100%;
    height:100%;
    margin-top:2%;
    margin:0 auto;
}
.crewCard_item_wrap{
    margin-top:2%;
}
.category_title{
    font-size: 1.5rem;
    font-weight: 600;
    display: inline;
}
.category_more{
    float:right;
    margin-right:20px;
    font-weight: 600;
    font-size: 20px;
    text-decoration: underline;
}
.category_more:hover{
    color:rgb(242, 170, 170);
}
.crewCard_item{
    margin-bottom: 20px;
    background-color: white;
    border-radius: 15px;
    display: inline-block;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(119,119,119,1);
    -moz-box-shadow: 1px 1px 5px 0px rgba(119,119,119,1);
    box-shadow: 1px 1px 5px 0px rgba(119,119,119,1);
    width:90%;
}
.crewCard_img{
    width:100%;
    height:160px;
}
.crewCard_img img{
    width:100%;
    height:100%;
    border-radius: 15px 15px 0px 0px;
}
.crewCard_desc{
    /* padding:5%; */
    height:120px;
    width: 90%;
    margin: 0 auto;
    overflow: hidden;
    font: small-caption;
}
.crewCard_desc h3{
    font-weight: 600;
    margin:10px 0px;
    font-size: 16px;
    text-overflow:ellipsis;overflow: hidden;white-space: nowrap;
}
.crewCard_desc p{
    font-size: 14px;
    margin-top: 3px;
    padding-right: 5px;
}

@media (max-width: 768px) {
    .crewCard{
        width:90%;
        margin:0 auto;
    }
    .crewCard_item{
        width: 100%;
    }
    .crewCard_item_wrap {
        margin-top: 5%;
    }
    /* .crewCard_item:nth-child(2) {
        margin-right:0px;
    } */
    /* .crewCard_item:nth-child(n+3) {
        display: none;
    } */
    .crewCard_desc p{
        font-size: 12px;
        margin-top: 3px;
        padding-right: 5px;
    }

}