@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    width: 100%;
    height:50px;
    position: fixed;
    background-color: rgb(242, 170, 170);
    top:0;
    z-index: 110;
}
.top{
    width:80%;
    margin:0 auto;
}
.top .logo a{
    font-size:30px;
    font-weight: 600;
    color:white;
    display: inline-block;
    padding: 8px 0px 0px 0px;
}
.top .logo{
    display: inline-block;
}
.top .navigation{
    float:right;
}
.navigation > .pc_navigation ul li{
    float:left;
    color:white;
    font-weight: 600;
    margin-top: 2px;
    padding:15px 0px;
}
.navigation > .pc_navigation ul li:hover{
    background-color: white;
    border-radius: 25px;
}
.navigation > .pc_navigation ul li a{
    color:white;
    padding:15px;
}
.navigation > .pc_navigation ul li a:hover{
    color: rgb(242, 170, 170);
}
.navi_click{
    background-color: white;
    border-radius: 25px;
}
.navi_click > a{
    color: rgb(242, 170, 170) !important;
}

@media (max-width: 768px) {
    .navigation > .pc_navigation{
        display: none;
    }
}
.footer{
    width:100%;
    height:50px;
    margin-top:50px;
    border-top:1px solid #e3e3e3;
}
.pc_footer{
    width:80%;
    margin:0 auto;
    padding:30px 0px 50px 0px;
}
.copyright{
    float:left;
}
.sitemap{
    float:left;
    margin-left:50px;
}
.sitemap ul li{
    float: left;
    padding-left:10px;
}
.SNS{
    float:right;
    margin-right:50px;
}
.SNS ul li{
    padding-left:20px;
    float:left;
}
.mob_menu{
    display: none;
    background-color: rgb(242, 170, 170);
    width:100%;
    height:50px;
    position: fixed;
    margin-top: 50px;
    bottom: 0;
    box-shadow: 0px -1px 3px 0px rgba(119,119,119,1);
}
.mob_menu ul{
    display: flex;
}
.mob_menu ul li{
    float:left;
    color:white;
    font-weight: 600;
    flex:1 1;
    text-align: center;
}
.mob_menu ul li a{
    color:white;
    display: inline-block;
    padding: 17px 0px;
}

@media (max-width: 768px) {
    .footer{
        margin-top: 0px;
    }
    .pc_footer{
        display: none;
    }
    .mob_menu{
        display: block;
    }
    .mobile_navi_click{
        color:red !important;
    }
}
.map_table_wrap{
    width:100%;
}
.display_type, .map_table_wrap{
    z-index: 100 !important;
    position: absolute;
    background: white;
    border:1px solid black;
    border-radius: 25px;
}
.display_type{
    right: 0px;
    
}
.display_type ul li{
    float:left;
    padding:5px 15px;
    border-right:1px solid black;
    font-weight: 600;
    font-size:18px;
}
.list_notSelected{
    background-color: white;
    border-radius: 0px 15px 15px 0px;
}
.list_selected{
    background: #e3e3e3;
    border-radius: 0px 15px 15px 0px;
}
.map_selected{
    background: #e3e3e3;
    border-radius: 15px 0px 0px 15px;
}
.map_notSelected{
    background-color: white;
    border-radius: 15px 0px 0px 15px;
}
.map_table_wrap{
    width: 35%;
    right: 0px;
    top: 20%;
    height: 350px;
    padding:0px 10px;
}
.map_table{
    width:100;
    margin:0 auto;
}
.map_table h1{
    font-size: 18px;
    font-weight: 600;
    padding: 15px 0px;
}

.map_table ul{
    /* display: flex; */
    width:100%;
}
.map_table ul li{
    /* flex:1; */
    width:24%;
    float:left;
    text-align: center;
    border:1px solid #cccccc;
    padding:7px 0px;
    height:15px;
}
.map_table ul li{
    border-bottom: none;
    border-right: none;
}
.map_table ul li:nth-child(4n){
    border-right: 1px solid #cccccc;
}
.map_table ul li:nth-child(n+25):nth-child(-n+28){
    border-bottom: 1px solid #cccccc;
}
.map_table ul li:nth-child(4n+1){
    clear:left;
}
.map_info{
    padding: 10px 15px;
    float:left;
    margin-top: 15px;
}
.area_color{
    color:red;
    font-weight: 600;
}

@media (max-width: 768px) {
    #react-naver-map div:nth-child(5){
        display: none;
    }
    .map_table_wrap{
        width: 50%;
        top: 10%;
        height: 305px;
        padding: 0px 10px;
    }
    .map_table ul li{
        font-size: 15px;
        height: 14px;
    }
    .map_table ul li:hover{
        background-color: #69C;
        color:white;
    }
    .map_info{
        margin-top: 0;
    }
}
@media (max-width: 500px) {
    .map_table_wrap{
        width: 65%;
    }
    .map_table h1 {
        padding:12px 0px 10px 0px;
    }
    .map_table ul li{
        font-size: 14px;
    }
    
}
.content{
    margin-top:50px;
    width:100%;
}
.map{
    height:500px;
}
@media (max-width: 768px) {
    .map{
        height:350px;
    }
}
@media (max-width: 424px) {
}
.crew_category_item{
    width:100%;
}
.crew_category_item:nth-child(2n){
    background-color: rgb(255, 235, 235);
}

.crew_category,.crew_list_wrap{
    width:80%;
    height:100%;
    margin:0 auto;
    padding:20px 0px;
}
.crew_category h1{
    display: inline-block;
    font-size: 26px;
    font-weight: 600;
}
.crew_category select{
    width:100px;
    height:35px;
    margin:0px 15px;
    font-size: 18px;
}
.area2{
    display: inline-block;
    margin-left: 25px;
}
@media (max-width: 768px) {
    .crew_category h1{
        display: inline-block;
        font-size: 18px;
        font-weight: 600;
    }
    .crew_category select{
        width:100px;
        height:35px;
        margin:0px 5px;
        font-size: 18px;
    }
    .area2{
        margin-left:10px;
    }
}
@media (max-width: 480px) {
	.crew_list_wrap{
        width:95%;
    }
}
.crewCard_container{
    background-color: rgb(255, 235, 235);
    padding:2% 0%;
}
.crewCard{
    width:100%;
    height:100%;
    margin-top:2%;
    margin:0 auto;
}
.crewCard_item_wrap{
    margin-top:2%;
}
.category_title{
    font-size: 1.5rem;
    font-weight: 600;
    display: inline;
}
.category_more{
    float:right;
    margin-right:20px;
    font-weight: 600;
    font-size: 20px;
    text-decoration: underline;
}
.category_more:hover{
    color:rgb(242, 170, 170);
}
.crewCard_item{
    margin-bottom: 20px;
    background-color: white;
    border-radius: 15px;
    display: inline-block;
    box-shadow: 1px 1px 5px 0px rgba(119,119,119,1);
    width:90%;
}
.crewCard_img{
    width:100%;
    height:160px;
}
.crewCard_img img{
    width:100%;
    height:100%;
    border-radius: 15px 15px 0px 0px;
}
.crewCard_desc{
    /* padding:5%; */
    height:120px;
    width: 90%;
    margin: 0 auto;
    overflow: hidden;
    font: small-caption;
}
.crewCard_desc h3{
    font-weight: 600;
    margin:10px 0px;
    font-size: 16px;
    text-overflow:ellipsis;overflow: hidden;white-space: nowrap;
}
.crewCard_desc p{
    font-size: 14px;
    margin-top: 3px;
    padding-right: 5px;
}

@media (max-width: 768px) {
    .crewCard{
        width:90%;
        margin:0 auto;
    }
    .crewCard_item{
        width: 100%;
    }
    .crewCard_item_wrap {
        margin-top: 5%;
    }
    /* .crewCard_item:nth-child(2) {
        margin-right:0px;
    } */
    /* .crewCard_item:nth-child(n+3) {
        display: none;
    } */
    .crewCard_desc p{
        font-size: 12px;
        margin-top: 3px;
        padding-right: 5px;
    }

}
.crewCardList_item_wrap{
    display: inline-block;
}
.crewCardList_item{
    background-color: white;
    border-radius: 15px;
    display: inline-block;
    margin-right: 1%;
    flex:1 1;
    box-shadow: 1px 1px 5px 0px rgba(119,119,119,1);
    width: 45%;
    margin-right: 3%;
    margin-top:3%;
    height: 200px;
}
.crewCardList_box{
    height: 100%;
}
.crewCardList_img{
    width: 40%;
    float:left;
    margin-right: 20px;
}
.crewCardList_img img{
    width:100%;
    height: 200px;
    border-radius: 15px 0px 0px 15px;
}
.crewCardList_desc{
    height: 100%;
    padding-right: 8px;
    padding-top: 20px;
}
.crewCardList_box h3{
    font-weight: 600;
    padding-bottom: 10px;
    font-size: 19px;
    text-overflow:ellipsis;overflow: hidden;white-space: nowrap;
}
.crewCardList_box p {
    font-size: 17px;
    margin-top: 7px;
}
.crewCardList_desc_info{
    margin-top: 50px;
}
@media (max-width: 768px) {
    .crewCardList{
        width: 90%;
        margin: 0 auto;
    }
    .crewCardList_item{
        width:100%;
        height:150px;
        margin-top: 20px;
    }
    .crewCardList_img img{
        height: 150px;
    }
    .crewCardList_desc_info{
        margin-top: 20px;
    }
    .crewCardList_box h3{
        padding-bottom: 0px;
        font-size: 16px;
    }
    .crewCardList_box p {
        font-size: 14px;
    }
    
}
.sub_title{
    color:black;
    font-weight: 600;
}
.crewDetail_wrap{
    width: 60%;
    margin:0 auto;
}
.crewDetail_wrap >h1{
    margin-top: 100px;
    font-size: 36px;
    font-weight: 600;
}
.crewDetail_info{
    margin-top:100px;
}
.crewDetail_info img{
    border-radius: 25px;
    float:left;
    margin-right:50px;
    width:35%;
    height: 220px;
}
.crewDetail_desc{
    width:80%;
    margin: 0 auto;
}
.crewDetail_desc h3{
    font-size: 26px;    
    font-weight: 600;
    padding:10px 0px;    
}
.crewDetail_desc p{
    padding:20px;    
    margin-top: 10px;
    font-size: 22px;    
    border:1px solid #e3e3e3;
    line-height: 30px;
}
.crewDetail_desc p:last-child{
    min-height: 320px;
}
@media (max-width: 768px) {
    .crewDetail_wrap {
        width: 80%;
        margin-bottom: 50px;
    }
}

@media (max-width: 480px) {
    
    .crewDetail_info img{
        width:45%;
        margin-right:20px;
        height: 140px;
    }
    .crewDetail_desc h3{
        font-size: 20px;    
        font-weight: 600;
        padding:10px 0px;    
    }
    .crewDetail_desc p{
        margin-top: 10px;
        font-size: 18px;    
    }
}
.crewCreate{
    width:50%;
    margin:0 auto;
    padding:30px 0px;
}
.crewCreate h1{
    font-size:20px;
    font-weight: 600;
    margin: 20px 0px;
}
.crewCreate input{
    height:30px;
    padding:5px;
    font-size:16px;
}
.crewCreate textarea{
    padding:5px;
    font-size:16px;
}
.area{
    margin-bottom: 20px;
}
.category select, .area select{
    margin-right: 15px;
    height: 40px;
    width: 30%;
    font-size: 16px;
    border: 1px solid #e3e3e3;
    border-radius: 5px;
    box-shadow: 0px 0px 1px 0px rgba(0,0,0,0.75);
    outline: none;
    background-color: white;
}
.area select{
    font-size: 18px;
}
.area span{
    font-weight: 600;
    font-size: 18px;
    margin-right: 20px;
    border-top: 1px solid #bbbbbb;
    border-bottom: 1px solid #bbbbbb;
    color:#808080;
    display: inline-block;
    width: 30%;
    height: 40px;
    vertical-align: top;
    line-height: 40px;
    text-align: center;
}
.category select option{
    font-size: 16px;
}
.recruit h1{
    display: inline-block;
    margin-right: 30px;
}
/* checkbox */
.recruit input:nth-child(2){
    vertical-align: middle;
    zoom:1.3;
    margin-left:10px;
}
/* data field */
.recruit input:nth-child(3), .range{
    display: block;
    width: 60%;
}

.metting_date input[type=date]{
    background-color: white;
    border:1px solid #e3e3e3;
}

.writing input, .writing textarea{
    width:100%;
}

.writing_desc textarea{
    height:200px;
}
.thumbnail input{
    border:1px solid black;
}
.profile_preview{
    margin-top:20px;
    width: 280px;
    height: 160px;
}
.submit_button button{
    width:100%;
    outline: none;
    border-radius: 15px;
    background-color: rgb(242, 170, 170);
    color:white;
    border:1px solid #d3d3d3;
    margin:30px 0px;
    height:40px;
    font-size: 18px;
    font-weight: 600;
}


@media (max-width: 1024px) {
	.crewCreate{
        width:80%;
    }
}

@media (max-width: 768px) {
    .category select, .area select{
        width: 35%;
    }
    .area span{
        width: 35%;
    }
    .recruit input:nth-child(3), .range{
        width: 70%;
    }
}

@media (max-width: 480px) {
    .crewCreate{
        width:90%;
        margin-left:15px;
    }
    .category select, .area select{
        width: 40%;
    }
    .area span{
        width: 40%;
    }
    .recruit input:nth-child(3), .range{
        width: 80%;
    }
    .crewCreate input, .crewCreate select ,.crewCreate textarea {
        border: none;
        border-bottom: 1px solid #e3e3e3;
        border-top: 1px solid #e3e3e3;
        padding-left:5px;
    }
    
    input[type=file]{
        border:none;
    }
    
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    font-family: 'Noto Sans KR', sans-serif;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
a{
    text-decoration: none;
    color:black;
}
a:hover{
    text-decoration: none;
    color:black;
}
